import ReactModal from "react-modal";
import React from "react";
import { useState, useEffect } from "react";

export default function Description() {
  const [isModalOpen, setIsModalOpen] = useState(false);

  function closeModal() {
    setIsModalOpen(false);
  }

  function handleSeeMoreClick() {
    setIsModalOpen(true);
  }

  useEffect(() => {
    const handleOutsideClick = (e) => {
      if (isModalOpen) {
        if (!e.target.closest("[data-modal]")) {
          closeModal();
        }
      }
    };

    document.addEventListener("mousedown", handleOutsideClick);

    return () => {
      document.removeEventListener("mousedown", handleOutsideClick);
    };
  }, [isModalOpen]);

  return (
    <section className="description">
      <h2>Profil</h2>
      <div className="container">
        <div className="photo-container">
          <img
            className="photo"
            src="https://storage.googleapis.com/images_portfolio_steam/photo-cv.webp"
            alt="Clment Bartholom"
          />
        </div>
        <p>
          Dans l'univers infini du dveloppement web, plongez dans une aventure
          pique aux cts de Clment Bartholom. Recrutez-le pour tendre votre
          empire numrique.
        </p>
        {/* <p>
          Clment Bartholom est un dveloppeur web passionn par le front-end
          et le webdesign. Dans un monde saisissant de ralisme, recrutez-le et
          tendez votre empire numrique.
        </p> */}
        <button onClick={handleSeeMoreClick}>En savoir plus</button>

        <span>valuations : extrmements positives</span>
        <span>Date de parution : 17 janvier 1996</span>
      </div>
      <span>Tags populaires :</span>
      <div className="tag-list">
        <span className="tag">Coopration</span>
        <span className="tag">Logique</span>
        <span className="tag">Programmation</span>
      </div>

      <ReactModal
        isOpen={isModalOpen}
        onRequestClose={closeModal}
        contentLabel="Project Modal"
        appElement={document.getElementById("root")}
        data-modal
        style={{
          content: {
            padding: "0px",
            width: "100%",
            height: "100%",
            margin: "auto",
            background: "#1b2838",
            inset: "initial",
          },
        }}>
        <div className="description-modal" data-modal>
          <span
            className="close-modal-btn"
            onClick={closeModal}
            aria-label="close">
            &times;
          </span>
          <h2>Bienvenue sur mon portfolio !</h2>
          <p>
            Si vous tes adepte de jeux vido, notamment sur PC, vous aurez
            normalement remarqu que ce portfolio s'inspire de la clbre
            plateforme Steam (sinon, je devrais sans doute revoir mon
            design...). Ce choix reflte mon identit et mon parcours :
            journaliste de formation, j'ai travaill pendant prs de cinq ans
            comme rdacteur freelance pour le site jeuxvideo.com (entre autres
            mdias).
            <br></br>
            <br></br>
            Passionn par le monde du web, j'ai dcid de me reconvertir dans le
            dveloppement informatique pour mettre  profit ma crativit et mes
            comptences techniques. J'aime concevoir des sites modernes,
            fonctionnels et accessibles qui rpondent aux besoins des
            utilisateurs. Quand je ne code pas, vous me trouverez srement en
            train de lire un roman de science-fiction, de jouer du piano ou de
            regarder un match de basketball.
            <br></br>
            <br></br>
            Mon objectif actuel : trouver une alternance au sein d'une
            entreprise de Nouvelle-Aquitaine afin de prparer un diplme Bac
            +3/4 et enrichir mes comptences. Pour en savoir plus sur mes
            expriences professionnelles et scolaires, je vous invite  cliquer
            sur les "succs" en bas de page.
          </p>
        </div>
      </ReactModal>
    </section>
  );
}
